    font-family: Rubik;
    font-size: 60px;
    font-style: normal;
    font-weight: 800;
    letter-spacing: 10px;
    color: #EA6D67;
}

.main__name2 {
    text-align: center;
    font-family: 'Pacifico', system-ui;
    font-weight: 400;
    font-size: 60px;
    color: #A0222E;
    margin-bottom: 20px;
}

.main__name3 {
    text-align: center;
    font-family: 'Rubik', system-ui;
    font-weight: 400;
    font-size: 40px;
    color: #EA6D67;
}

.activities {

}

